// 
import { reqGetCoupon } from '@/api/index.js'

const state = {
	// 
	couponList: []
}

const actions = {
	// 
	async getCoupon({ commit }) {
		const res = await reqGetCoupon()
		if (res.code === 200) commit('setCoupon', res.data)
	}
}

const mutations = {
	// 
	setCoupon(state, data) {
		state.couponList = data
	}
}

const getters = {}

export default {
	state,
	mutations,
	actions,
	getters
}