// 
import { reqGetPrivacy, reqUpload } from '@/api/index.js'

const state = {
	content: ''
}

const actions = {
	// 
	async getPrivacy({ commit }, data) {
		const res = await reqGetPrivacy(data)
		if (res.code === 200) commit('setPrivacy', res.data)
	},
	// 
	async reqUpload() {
		const res = await reqUpload()
		console.log(res)
	}
}

const mutations = {
	// 
	setPrivacy(state, data) {
		state.content = data
	}
}

const getters = {}

export default {
	state,
	mutations,
	actions,
	getters
}